<template>
    <div id="index">
        <Header title="主体智能查验系统"></Header>
      <div class="index">
        <img style="width: 50%; height: 50%;float: left;margin-left: 10%;margin-top: 45px" src="../../assets/index.jpg">
      </div>
        <div class="tableContainer">
            <div class="tableRow">
                <p><i class="fa fa-user"></i></p>
                <p><input class="side" type="text" v-model="userInfo.email" placeholder="输入邮箱"></p>
            </div>
            <div class="tableRow">
                <p><i class="fa fa-lock"></i></p>
                <p><input class="side" type="password" v-model="userInfo.password" placeholder="输入密码"></p>
            </div>
        </div>
        <div class="tableContainer_button">
            <div class="tableRow_button">
                <input type="button" @click="login" value="登录">
            </div>
            <div class="tableRow_button">
                <input type="button" @click="register" value="注册">
            </div>
        </div>
        <!--        <div>-->
        <!--            <input type="button" @click="login" value="登录">-->
        <!--            <input type="button" @click="register" value="注册">-->
        <!--        </div>-->
        <Footer></Footer>
    </div>
</template>

<script>
import Header from '../../components/Header'
import Footer from '../../components/Footer'
import Cookies from "js-cookie";

export default {
  name: 'index',
  components: {
    Header,
    Footer
  },
  data: function () {
    return {
      userInfo: {email: '', password: ''}
    }
  },
  methods: {
    login: function () {
      if (!this.userInfo.email || !this.userInfo.password) {
        this.msgError("输入邮箱/密码")
        return;
      }
      this.$request({
        url: '/user/login?email=' + this.userInfo.email + '&password=' + this.userInfo.password,
        method: 'post',
        data: this.userInfo,
      }).then((response) => {
        let temp = JSON.parse(response.data)
        if (temp.id === 1) {
          this.$router.push({path: '/manages'}).catch(err => {});
        } else {
          this.$router.push({path: '/rulesCreated'}).catch(err => {});
        }
        window.sessionStorage.setItem('user', temp.id);
        this.msgSuccess("登录成功");
      });
    },
    register: function () {
      this.$router.push({path: '/register'}).catch(err => {});
    }
  },
}
</script>

<style scoped>

    .tableContainer {
        font-size: 20px;
        display: table;
        margin-left: auto;
        margin-right: 250px;
        margin-top: 150px;
    }

    .tableRow {
        display: table-row;
    }

    .tableRow p {
        display: table-cell;
        padding: 15px;
    }

    .tableRow p:last-child {
        font-size: 20px;
        padding: 20px;
    }

    .side {
        font-size: 18px;
        padding: 5px;
    }

    .tableContainer_button {
        display: table;
        margin-left: auto;
        margin-right: 300px;
        margin-bottom: 150px;
    }

    .tableRow_button {
        display: table-row;
    }

    .tableRow_button input {
        display: table-cell;
        background-color: #0c4469;
        color: white;
        width: 200px;
        height: 30px;
        padding: 5px;
        font-size: 18px;
        letter-spacing: 3px;
        line-height: 10px;
        border-radius: 5px;
        margin-bottom: 10px;
    }

</style>



